<template>
  <div>
    <Alert></Alert>
    <loading :active.sync="isLoading"></loading>
    <header>
      <NavBar />
    </header>
    <div>
      <ol class="stepBar">
        <li class="visited"><span>1</span><br></li>
        <li><span>2</span><br></li>
        <li><span>3</span><br></li>
      </ol>
    </div>
    <div class="container">
      <div class="row flex-row-reverse">
                <!--  -->
        <div class="col-md-5 mb-3">
          <table class="table">
            <thead>
              <tr>
                <th colspan="4" class="border-top-0 h4 text-main"></th>
              </tr>
            </thead>
            <tbody>
              <!-- item.id  key  item.id -->
              <tr v-for="(item) in cartItem.carts" :key="item.id">
                <td width="30px">
                  <!-- ( ID) -->
                  <button class="btn btn-outline-danger" @click="removeCartItem(item.id)">
                    <i class="fas fa-spinner fa-spin" v-if="item.id === status.loadingItem"></i>
                    <i class="far fa-trash-alt" v-else></i>
                  </button>
                </td>
                <td><img :src="item.product.imageUrl" style="height: 40px; width: 38px" class="img-thumbnail">
                  {{ item.product.title }}
                  <div class="text-success" v-if="item.coupon"></div>
                </td>
                <td width="80">
                  {{ item.qty }} / {{ item.product.unit }}
                </td>
                <td class="text-right">
                  {{ item.total | currency }}
                </td>
              </tr>
              <tr>                           
                <td colspan="3" class="text-right h4"></td>
                <td colspan="1" class="text-right h4" :class="{'through-line': cartItem.final_total !== cartItem.total}">
                  {{ cartItem.total | currency }}
                </td>
              </tr>
              <!--  -->
              <tr v-if="cartItem.final_total !== cartItem.total">
                <td colspan="3" class="text-right text-success h3"></td>
                <td colspan="1" class="text-right text-success h3">
                  {{ cartItem.final_total | currency }}
                </td>
              </tr>
            </tbody>
          </table>
          <p class="text-main"> costdown </p>
          <!--  -->
          <div class="input-group mb-3 input-group-sm">            
            <!--  coupon_code  -->
            <input type="text" class="form-control" v-model="coupon_code" placeholder="">
            <div class="input-group-append">
              <button class="btn btn-outline-third" type="button" @click="addCoupenCode">
                
              </button>
            </div>
          </div>
        </div>
        <!--  -->
        <!--  form  submit  -->
        <ValidationObserver class="col-md-7 font-weight-bolder" ref="observer" v-slot="{ invalid }" tag="form"
          @submit.prevent="createOrder">
          <h4 class="text-main"></h4>
          <div class="form-row mb-3">
            <ValidationProvider name="" rules="required" v-slot="{ errors }" class="col-md-6">
              <label for="username" class="text-main"></label>
              <input type="text" class="form-control" name="name" id="username" v-model="form.user.name"
                placeholder="" :class="{'is-invalid': errors[0]}">
              <span class="text-danger">{{ errors[0] }}</span>
            </ValidationProvider>
            <ValidationProvider name="" rules="required|min:10" v-slot="{ errors }" class="col-md-6">
              <label for="usertel" class="text-main"></label>
              <input type="tel" class="form-control" id="usertel" v-model="form.user.tel" placeholder=""
                :class="{'is-invalid': errors[0]}">
              <span class="text-danger">{{ errors[0] }}</span>
            </ValidationProvider>
          </div>
          <div class="form-group">
            <ValidationProvider name="Email" rules="required|email" v-slot="{ errors }" ref="emailField">
              <label for="useremail" class="text-main">Email</label>
              <input type="email" class="form-control" name="email" id="useremail" v-model="form.user.email"
                placeholder=" Email" :class="{'is-invalid': errors[0]}">
              <span class="text-danger">{{ errors[0] }}</span>
            </ValidationProvider>
          </div>
          <div class="form-group">
            <ValidationProvider name="" rules="required" v-slot="{ errors }">
              <label for="useraddress" class="text-main"></label>
              <input type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address"
                placeholder="" :class="{'is-invalid': errors[0]}">
              <span class="text-danger">{{ errors[0] }}</span>
            </ValidationProvider>
          </div>
          <div class="form-group">
            <label for="comment" class="text-main"><span class="text-muted">()</span></label>
            <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
          </div>
          <div class="text-right">
            <button class="btn btn-main btn-block" :disabled="invalid"></button>
          </div>
        </ValidationObserver>
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
  import NavBar from "../NavBar";
  import Footer from "../Footer";
  import $ from "jquery";
  // 
  import Alert from '../AlerMessage';

  export default {
    name: 'Checkout',
    components: {
      NavBar,
      Footer,
      Alert,
    },
    data() {
      return {
        // 
        form: {
          user: {
            name: '',
            email: '',
            tel: '',
            address: '',
          },
          message: '',
        },
        // 
        cartItem: [],
        // 
        status: {
          loadingItem: '',
        },
        //  coupon_code 
        coupon_code: '',
        isLoading: false,
      }
    },
    methods: {
      // 
      getCart() {
        const vm = this;
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
        this.$http.get(api).then((response) => {
          vm.cartItem = response.data.data;
        });
      },
      // 
      removeCartItem(id) {
        const vm = this;
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`
        vm.isLoading = true;
        vm.status.loadingItem = id;
        this.$http.delete(api).then((response) => {
          vm.status.loadingItem = '';
          // 
          vm.getCart();
          vm.isLoading = false;
        });
      },
      // 
      addCoupenCode() {
        const vm = this;
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`
        vm.isLoading = true;
        //  API 
        const coupon = {
          code: vm.coupon_code,
        }
        this.$http.post(api, {
          data: coupon
        }).then((response) => {
          // 
          if (response.data.success) {
            // 
            vm.getCart();
            vm.isLoading = false;
          } else {
            vm.$bus.$emit('message:push', response.data.message, 'danger');
            vm.isLoading = false;
          }
        });
      },
      // 
      createOrder() {
        const vm = this;
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
        const isValid = vm.$refs.observer.validate();
        const order = vm.form;
        if (!isValid) {
          vm.$bus.$emit('message:push', '', 'danger');
          vm.isLoading = false;
        } else {
          this.$http.post(api, {
            data: order
          }).then((response) => {
            if (response.data.success) {
              vm.$router.push(`/pay/${response.data.orderId}`);
            } else {
              vm.$bus.$emit('message:push', response.data.message, 'danger');
              vm.isLoading = false;
            }
          });
        }
      },
    },
    created() {
      this.getCart();
    },
  }

</script>



